<template><section><h1>GroundPolylineGeometry</h1>
<p>The <code v-pre>vc-geometry-polyline-ground</code> component is used to load a polyline on terrain or 3D Tiles. Only to be used with GroundPolylinePrimitive. Need to be used as a subcomponent of <code v-pre>vc-instance-geometry</code>. Only can be mounted to <code v-pre>vc-primitive-polyline-ground</code>.</p>
<h2>Example</h2>
<h3>Load GroundPolylineGeometry with <code v-pre>vc-geometry-polyline-ground</code></h3>
<h4>preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-polyline-ground :appearance="appearance">
          <vc-instance-geometry>
            <vc-geometry-polyline-ground ref="polylineGround" :positions="positions" :width="2"></vc-geometry-polyline-ground>
          </vc-instance-geometry>
        </vc-primitive-polyline-ground>
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-layer-imagery>
          <vc-provider-imagery-mapbox mapId="mapbox.streets"></vc-provider-imagery-mapbox>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
