<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>required</code> An array of Cartesian3 defining the polyline's points. Heights above the ellipsoid will be ignored. <strong>Structure: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> The screen space width in pixels.</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> The distance interval in meters used for interpolating options.points. Defaults to 9999.0 meters. Zero indicates no interpolation.</td>
</tr>
<tr>
<td>loop</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> Whether during geometry creation a line segment will be added between the last and first line positions to make this Polyline a loop.</td>
</tr>
<tr>
<td>arcType</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> The type of line the polyline segments must follow. Valid options are ArcType.GEODESIC and ArcType.RHUMB. <strong>NONE: 0, GEODESIC: 1, RHUMB: 2</strong></td>
</tr>
</tbody>
</table>
<p>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/GroundPolylineGeometry.html">GroundPolylineGeometry</a></strong></p>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: {},
          geometryInstances: {},
          positions: [
            { lng: 100.1340164450331, lat: 31.05494287836128 },
            { lng: 108.08821010582645, lat: 31.05494287836128 }
          ]
        }
      },
      mounted() {
        this.$refs.polylineGround.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
          const boundingSphere = Cesium.BoundingSphere.fromPoints(cesiumObject._positions)
          viewer.scene.camera.flyToBoundingSphere(boundingSphere)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.appearance = new Cesium.PolylineMaterialAppearance()
        }
      }
    }
  </script>